import React, { Component } from "react";
import { List, NavBar, Icon } from "antd-mobile";

const Item = List.Item;
const Brief = Item.Brief;

import { get } from "util/fetch";
import "./index.less";

const routePath = "/api/english/collection";
export default class index extends Component {
  state = {
    data: []
  };
  componentDidMount() {
    this.init();
  }
  init = () => {
    get(routePath)
      .then(res => {
        log(res);
        this.setState({
          data: res.data
        });
      })
      .catch(err => {
        log(err);
      });
  };
  handleClick = id => {
    this.props.history.push(`/collection-word?collection_id=${id}`);

    log(id);
  };
  render() {
    const navBar = (
      <NavBar mode="light" rightContent={[<Icon key="1" type="ellipsis" />]}>
        I will get it
      </NavBar>
    );
    const list = (
      <div>
        {this.state.data.map(item => (
          <Item
            key={JSON.stringify(item)}
            arrow="horizontal"
            multipleLine
            onClick={() => this.handleClick(item.id)}
          >
            {item.name} <Brief>{item.note}</Brief>
          </Item>
        ))}
      </div>
    );
    return (
      <div className="collection-container">
        {navBar}
        <h2>English vocabulary</h2>
        <List className="my-list">{list}</List>
      </div>
    );
  }
}
